let _this;
export default {
  name: "nav-top",
  components:{

  },
  data(){
    return {
      //修改密码
      modifyPsdModal:false,
      psdInfo: {
        oldPassword:"",
        newPassword:"",
        confirmPassword:"",
      },
      psdVerify: {
        newPsdFlag:false,
        cofPsdFlag:false,
        samePsdFlag:false,
      },
      /*其它modal*/
      exitModal:false, //退出登录
      loginModal:false,  //提示重新登录
    }
  },
  mounted(){
    _this = this;
    _this.init();
  },
  watch:{
    loginModal:{
      handler(nv){
        if(nv === false){
          this.reLogin();
        }
      }
    }
  },
  methods:{
    init(){
      $(document).bind('click', function (e) {
        if (!(e.target.className === 'personal-menu-btn' || $(e.target).parent('.personal-menu-btn').length > 0)) {
          $('.personal-menu-ul').fadeOut();
          $(this).find('.personal-menu-ul i.arrow').removeClass('open');
        }
      })
    },
    /*收缩菜单*/
    shrinkMenu: () => {
      _this.$root.menu.shrink = _this.$root.menu.shrink + 1;
      if (_this.$root.menu.shrink == 1) {
        localStorage.setItem("shrinkStr", 'closed');
      }
      if (_this.$root.menu.shrink == 2) {
        localStorage.setItem("shrinkStr", 'retract');
      }
      if (_this.$root.menu.shrink == 3) {
        _this.$root.menu.shrink = 0;
        localStorage.setItem("shrinkStr", 'open');
      }
      let timer = setTimeout(function () { /*动画结束后获取内容宽度*/
        clearTimeout(timer);
        _this.$root.contentWidth = $('#page-content')[0].clientWidth;
      },300)
    },
    //跳转回home
    returnHome: () => {
      _this.$router.push({
        path: "/home"
      });
      _this.$root.menu.navTitle = 'M_Dashboard';
      _this.$root.menu.childTitle = 'M_Dashboard';
      _this.$root.menu.secondChildTitle = '';
      localStorage.setItem("menuTitle", type);
      localStorage.setItem("childMenuTitle", type);
      localStorage.setItem("secondChildTitle", '');
    },
    //打开管理员菜单
    openPersonalMenu: function (e) {
      e.stopPropagation();
      $('.personal-menu-ul').fadeToggle();
      $(this).find('i.arrow').toggleClass('open');
      //_this.$root.showNoticeBox = false;
    },

    // 注销登录
    logOut:function () {
      _this.exitModal = false;
      _this.$root.$router.push({path: '/login'});
    },
    reLogin:function(){
      _this.$router.push({path:'/login'});
      _this.loginModal=false;
    },
    /**
     * 修改管理员密码
     * */
    openPwdModal:function (){
      this.psdInfo = {
        oldPassword:"",
        newPassword:"",
        confirmPassword:"",
      };
      this.modifyPsdModal = true;
    },
    // 新旧密码判断一致
    judgePsd: function () {
      if (_this.psdInfo.newPassword && _this.psdInfo.confirmPassword) {
        if (_this.psdInfo.newPassword === _this.psdInfo.confirmPassword) {
          _this.psdVerify.samePsdFlag = false;
        } else {
          _this.psdVerify.samePsdFlag = true;
        }
      }
    },
    // 信息提交
    savePsdInfo:function () {
      _this.$validator.validateAll('passwordInfo').then(result =>{
        _this.judgePsd();
        if(result && !_this.psdVerify.samePsdFlag){
          let subData = JSON.parse(JSON.stringify(_this.psdInfo));
          subData.oldPassword = _this.$md5(subData.oldPassword).toLowerCase();
          subData.newPassword = _this.$md5(subData.newPassword).toLowerCase();;
          subData.confirmPassword = _this.$md5(subData.confirmPassword).toLowerCase();;
          _this.$ajax.post('/api/Account/ChangePassword?id='  + _this.$root.adminInfo.id, subData).then(function (res) {
            if(res.data.success){
              _this.messageNotice('success');
              _this.modifyPsdModal= false;
              _this.loginModal = true;
            }else{
              _this.messageNotice('error',res.data.message);
            }
          })
        }
      });
    },
  }
}



