let getMessageNotice = function (type, message, needFormat) {
  this.$Message.success({
    duration: type === 'error' ? 5 : 2,
    closable: type === 'error' || type === 'info',
    render: h => {
      if(type === 'success'){
        return h('div', [
          h('span', {
            class:['success-notice-bg']
          }, [
            h('span',{
              class:['utm-font utm-ok-icon success-notice-img'],
            })
          ]),
          h('div', {
            class:['success-notice-content']
          }, [
            h('span',{
              style:{
                'text-align':'left',
                'padding-left':'8px',
                'font-size':'16',
              },
              class:['message-title']
            }, message ? message:'Operated successfully!')
          ]),
        ])
      }else if(type === 'error'){
        return h('div', [
          h('span', {
            class:['success-notice-bg', 'error-notice-bg']
          }, [
            h('span',{
              class:['success-notice-img', 'utm-font utm-close-icon error-notice-img'],
            })
          ]),
          h('div', {
            class:['success-notice-content']
          }, [
            h('span',{
              style:{
                'text-align':'left',
                'padding-left':'8px',
                'font-size':'16px',
              },
              class:['message-title']
            }, 'Operation error!'),
            h('span',{
              style:{
                'text-align':'left',
                'padding-left':'8px',
                'white-space': needFormat ? 'pre' : 'normal',
                'word-break':'break-all',
              },
              class:['message-content']
            }, message)
          ]),
        ])
      }else if(type === 'info'){
        return h('div', [
          h('span', {
            class:['success-notice-bg','info-notice-bg']
          }, [
            h('span',{
              class:['success-notice-img','utm-font utm-icon-hide'],
            })
          ]),
          h('div', {
            class:['message-content']
          }, [
            h('span',{
              style:{
                'text-align':'left',
                'padding-left':'8px',
                'font-size':'16px',
              },
              class:['message-title']
            }, message)
          ]),
        ])
      }
    }
  });
};
export default getMessageNotice;



