let _this;
export default{
  name:"Menu",
  data(){
    return {

    }
  },
  mounted(){
    _this = this;
  },
  methods:{
    changeMenu:function(menu){
      _this.$root.menu.navTitle = menu.code;
      localStorage.setItem('navTitle',menu.code);
      if(_this.$route.path !== menu.route){
        _this.$router.push({path: menu.route})
      }
    },
  }
}



