// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App.vue'
import router from './router'

import axios from 'axios'
import iview from 'view-design'
import 'view-design/dist/styles/iview.css'
import locale from 'view-design/dist/locale/en-US'
import VeeValidate, {Validator} from 'vee-validate'
import moment from 'moment'
import md5 from 'js-md5'

// 自定义js
import './assets/js/jquery.min.js'
import messageNotice from './assets/js/messageNotice.js'
import times from './assets/js/times.js'
import common from './assets/js/common.js'
import menuData from './components/menu/menuInfo.js'

import '../static/font/iconfont.js' /*symbol图标*/

//验证账号密码
Validator.extend('passwordLength', {
  getMessage: field => "Passwords must be between 8 and 30 characters long.",
  validate: value => value.length > 7 && value.length < 31
});
Validator.extend('passwordNorm', {
  getMessage: field => "The password must contain at least one uppercase letter, lowercase letter, number and special symbol each",
  validate: value => /^(?![a-zA-Z0-9_]+$)(?![a-zA-Z\W_]+$)(?![a-z0-9\W_]+$)(?![A-Z0-9\W_]+$)/.test(value)
});
Validator.extend('required', {
  getMessage: (field) => {
    return field + " can not be empty";
  },
  validate: value => {
    return value || value === 0;
  }
});

Vue.use(iview, {locale});
Vue.use(VeeValidate, {
  fieldsBagName: 'fieldsBags',
  events: 'blur'
});

Vue.config.productionTip = false

window.eventBus = new Vue();
Vue.prototype.messageNotice = messageNotice;/*通知*/
Vue.prototype.$ajax = axios;
Vue.prototype.times = times;/*时间格式*/
Vue.prototype.$md5 = md5;
Vue.prototype.common = common;
Vue.prototype.moment = moment;

let _this;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  data(){
    return {
      version:"", //系统版本
      Authorization:localStorage.getItem('Authorization') ? localStorage.getItem('Authorization') : '',
      // 设置时间格式
      Time: {
        type: 'singapore',
        china: times.china,//中文
        singapore: times.singapore,//新加坡
      },
      // 请求
      isLoading: false, /*遮罩层*/
      loadingTimer: null, /*隐藏loading的定时器，用于防止两个接口先后调用时加载层快速闪烁*/
      loadingTimerDuration:50,
      requestCount:0,
      // 用户信息
      adminInfo: {},
      //菜单
      menu: {
        shrink: 0,
        menuList: [],
        navTitle: "",
      },
      //查看大图
      viewBigImage: {
        bigImgModal: false,
        srcStr: ""
      },
      contentWidth: '', /*内容区域宽度*/
      contentHeight: '', /*内容区域高度*/
      tableMaxHeight: '', /*table显示最大高度*/
      isTablePagerfixed: false,
    }
  },
  beforeMount() {
    _this = this;
    this.setInterceptor();
    if(window.location.hash !== '#/login' && _this.$route.path !== '/'){
      _this.getUserMenu();
    }
  },
  mounted() {

  },
  methods: {
    // 获取管理员信息
    getUserInfo:function () {
      this.$ajax.get('/api/AccessToken/GetLoginUser').then(function (res) {
        if(res.data.success){
          _this.adminInfo = res.data.data;
          _this.getSystemVersion();
        }else {
          _this.messageNotice('error',res.data.message);
        }
      })
    },
    // axios配置
    setInterceptor:function(){
      axios.defaults.baseURL = window.g.API_ROOT;
      // request 拦截器
      axios.interceptors.request.use(
        config => {
          config.withCredentials = true;
          config.headers.Authorization = _this.Authorization;
          // 是否打开加载页
          if (config.url && !config.headers.noLoading) {
            this.requestCount++;
            clearTimeout(this.loadingTimer);
            _this.isLoading = true;
          }
          return config;
        },
        error => {
          return Promise.reject(error);
        }
      );

      // response 拦截器
      axios.interceptors.response.use(
        response => {
          // 关闭加载层
          if (this.requestCount > 0 && !response.config.headers.noLoading) {
            _this.requestCount--;
          }
          if (this.requestCount === 0) {
            _this.loadingTimer = setTimeout(()=>{
              _this.isLoading = false;
            },_this.loadingTimerDuration);
          }
          if(response.status === 401){
            _this.messageNotice("error","Unauthorized");
            if(_this.$route.path !== "/login"){
              _this.$router.push({path: '/login'});
            }
          }
          return response;
        },
        error => {
          // 关闭加载页
          if (this.requestCount > 0) {
            _this.requestCount--;
          }
          if (this.requestCount == 0) {
            _this.loadingTimer = setTimeout(()=>{
              _this.isLoading = false;
            },_this.loadingTimerDuration);
          }
          if (error.response && error.response.status === 401){
            _this.messageNotice("error","Unauthorized");
            if(_this.$route.path !== "/login"){
              _this.$router.push({path: '/login'});
            }
          } else if (error.response && error.response.status === 404) {
            this.messageNotice('error', 'Request address does not exist');
          } else if (error.response && error.response.status === 500) {
            this.messageNotice('error', 'Abnormal internal server');
          } else if(error.response && error.response.config.url.indexOf('/connect/token') >-1){
            // 处理登录错误提示
            if(error.response.data.error_description){
              _this.messageNotice("error", error.response.data.error_description);
            }else{
              _this.messageNotice("error", "Data request failed");
            }
          } else {
            this.messageNotice('error', 'Data request failed');
          }
          return Promise.reject(error)
        }
      )
    },
    // 获取table显示最大高度
    getTableMaxHeight: function () {
      this.$nextTick(() => {
        let height1 = $('.utm-box-right').height() - 110;
        /*右侧内容高度*/
        let height2 = $('.utm-page-inquire').height();
        /*过滤列表高度*/
        this.tableMaxHeight = height1 - height2;
        if($('.utm-table').length>0){
          let height3 =  $('.utm-table .ivu-table-body table').height() + 100;
          if(height3 >= this.tableMaxHeight){
            this.isTablePagerfixed = true;
          }else{
            this.isTablePagerfixed = false;
          }
        }
      })
    },
    //获取登录用户菜单
    getUserMenu: function () {
      _this.menu.navTitle = localStorage.getItem('navTitle');
      _this.menu.menuList = menuData;
      let routes = [];
      _this.menu.menuList.forEach(item =>{
        routes.push({
          path: item.route,
          name: item.name,
          component: resolve => require([`@/components/${item.module}.vue`], resolve)
        });
      });
      routes.push({path: '*', redirect: '/notFound', meta: {}});
      routes.push({
        path: '/notFound',
        name: 'Not Found',
        component: resolve => require(['@/components/notfound/notfound.vue'], resolve)
      });
      router.addRoutes(routes);
      _this.getUserInfo();
    },
    // 获取系统版本
    getSystemVersion:function(){
      this.$ajax.get('/api/SystemSetting/GetVersion').then(function (res) {
        if(res.data.success){
          _this.version = res.data.data;
        }else {
          _this.messageNotice('error',res.data.message);
        }
      })
    },
    // 查看大图
    viewImage:function (str) {
      this.viewBigImage.srcStr = str;
      this.viewBigImage.bigImgModal = true;
    }
  }
});

router.beforeEach(function (to, from, next) {
  if(!_this.Authorization && to.path !== '/login'){
    next("/login");
  }else if(to.path === '/login'){
    _this.Authorization = '';
    _this.menu.navTitle = '';
    localStorage.setItem('Authorization','');
    localStorage.setItem('navTitle','');
    next();
  }else{
    next();
  }
});



