let _this,
sizeTimer;
import SideMenu from './components/menu/menu.vue'
import NavTop from './components/navTop/navTop.vue'

export default {
  name: 'App',
  components: {
    SideMenu,
    NavTop
  },
  data() {
    return {

    }
  },
  mounted() {
    _this = this;
    _this.getContentSize();
    window.onresize = ()=>{
      clearTimeout(sizeTimer);
      sizeTimer = setTimeout(()=>{
        _this.getContentSize();
        _this.$root.getTableMaxHeight();
      },200);
    }
  },
  watch:{
    '$root.menu.shrink':{
      handler(){
        this.$nextTick(()=>{
          this.getContentSize();
        })
      }
    }
  },
  methods: {
    getContentSize:function(){
      _this.$root.contentWidth = $('#page-content')[0].clientWidth;
      _this.$root.contentHeight = $('#page-content')[0].clientHeight;
    },
    // 关闭大图
    closeBigImg: function () {
      _this.$root.viewBigImage.bigImgModal = false;
      _this.$root.viewBigImage.srcStr = "";
    }
  }
}



