import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/login',
      name:'Default',
    },
    {
      path:'/login',
      name:'login',
      component:resolve => require(['@/components/login/login.vue'],resolve)
    }
  ]
})



